body {
  margin: 0px;
  padding: 0px;
  font-family: ndot-55;
  background-color: silver;
  padding-top: env(safe-area-inset-top);
}

.container {
  background-color: silver;
  height: 100vh;
  margin: 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  
}


.song-list-container {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  width: 30%;
  overflow: auto;
}

.song-list-item {
  color: black;
  width: 190px;
}

.song-list-item:hover {
  text-decoration: dashed;
  color: blue;
  border: 1px solid black;
}

.song-list-item-active {
  color: white;
  transition: color .8s;
}

@font-face {
  font-family: ndot-55;
  src: url("./fonts/fonts-ndot57.ttf");
}

@font-face {
  font-family: ndot-55;
  src: url("./fonts/fonts-ndot55.otf");
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.hoverImage {
  transition: transform 1s;
  transform-style: preserve-3d;
  animation: crazy-animation 2s ;
}

img {
  box-shadow: 4px 4px 10px grey;
  box-sizing: border-box;
  border: 20px solid silver;
  width: 40%;
  object-fit:cover;
}



@keyframes crazy-animation {
0% {
  transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
}
100% {
  transform: rotateX(180deg) rotateY(180deg) rotateZ(180deg);
}
/* 50% {
  transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
}
75% {
  transform: rotateX(540deg) rotateY(540deg) rotateZ(540deg);
}
100% {
  transform: rotateX(720deg) rotateY(720deg) rotateZ(720deg);
} */
}

@media only screen and (max-width: 800px) {
  .song-list-container {
    width: 70%; /* changes width to 100% on mobile */
    flex-direction: column;
  }
  img {
    width: 70%;
  }
  body::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: env(safe-area-inset-top);
    background-color: silver;
  }
  li {

    font-size: 20px;
    margin-top: 3px;
  }
}
